let initialState = {
    
       
        dialogs: [{id: 1, name: 'Anya'},
        {id: 2, name: 'Manya'},
        {id: 3, name: 'Vanya'},
        {id: 4, name: 'Gaffnya'},
        {id: 5, name: 'Tanya'}],
        messages: [{id: 1, message: 'Hi'},
        {id: 2, message: 'How are you'},
        {id: 3, message: 'ydo'},
        {id: 4, message: 'yo'},
        {id: 5, message: 'yo'}],

    
};
    

const dialogsReducer = (state = initialState, action) => {

    switch (action.type){
                   
        case 'SEND_MESSAGE':
            let body = action.newMessageBody;
            return {
                ...state,
                messages: [...state.messages, {id: 6, message: body}]
            };
        
        default:        
            return state;

    }
}
export default dialogsReducer;